import React from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Link from '@material-ui/core/Link';
import Box from '@material-ui/core/Box';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';

// Icon
import SchoolIcon from '@material-ui/icons/School';
import SettingsVoiceIcon from '@material-ui/icons/SettingsVoice';
import WorkIcon from '@material-ui/icons/Work';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary">
      {'Copyright  '}
      <Link color="inherit" href="https://material-ui.com/">
        Yuto Mori
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    minHeight: '100vh',
  },
  main: {
    marginTop: theme.spacing(8),
    marginBottom: theme.spacing(2),
  },
  footer: {
    padding: theme.spacing(3, 2),
    marginTop: 'auto',
    backgroundColor:
      theme.palette.type === 'light' ? theme.palette.grey[200] : theme.palette.grey[800],
  },
}));

export default function Home() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <CssBaseline />
      <Container component="main" className={classes.main} maxWidth="md">
        <Typography variant="h2" gutterBottom>Portfolio</Typography>
        <Typography variant="h5" gutterBottom>
          <Box textAlign="jusify">: Mori Yuto</Box>
          <Box textAlign="jusify">: Tmori.</Box>
          <Box textAlign="jusify">: </Box>
          <Box textAlign="jusify">: tmori9banner at gmail.com</Box>
        </Typography>
        <Box m={4} />

        <Typography variant="h4" gutterBottom></Typography>
        <Typography variant="h5" gutterBottom>
          <Box textAlign="jusify"> </Box>
          <Box textAlign="jusify"> </Box>
          <Box textAlign="jusify"> </Box>
        </Typography>
        <Box m={4} />

        <Typography variant="h4" gutterBottom></Typography>
        <Typography variant="h5" gutterBottom>
          <Box textAlign="jusify"> </Box>
          <Box textAlign="jusify">G (JDLA Deep Learning for GENERAL 2019 #3) </Box>
          <Box textAlign="jusify">
            <a href="https://bizsysd.enpit.jp/" target="_blank" rel="noopener noreferrer">enPiT-BizSysD</a>  () </Box>
          <Box textAlign="jusify">
            <a href="https://aibic.enpit.jp/" target="_blank" rel="noopener noreferrer">enPiT-AiBiC</a> AI () </Box>
        </Typography>
        <Box m={4} />

        <Typography variant="h4" gutterBottom></Typography>
        <List>
        <ListItem>
        <ListItemAvatar>
          <Avatar>
            <WorkIcon />
          </Avatar>
        </ListItemAvatar>
          <ListItemText primary="20193- IT">
            </ListItemText>
        </ListItem>
        </List>
        <Box m={4} />

        <Typography variant="h4" gutterBottom></Typography>
        <List>
        <ListItem>
        <ListItemAvatar>
          <Avatar>
            <SchoolIcon />
          </Avatar>
        </ListItemAvatar>
          <ListItemText primary="" 
                        secondary={
                          <React.Fragment>
                            <Typography>
                              <a href="https://www.ieice.org/ken/paper/20200309t1x2/">pdf</a>
                              </Typography>
                          </React.Fragment>
                        }>
            </ListItemText>
        </ListItem>
        <ListItem>
        <ListItemAvatar>
          <Avatar>
            <SchoolIcon />
          </Avatar>
        </ListItemAvatar>
          <ListItemText primary="">
            </ListItemText>
        </ListItem>
        </List>
        <Box m={4} />

        <Typography variant="h4" gutterBottom></Typography>
      <List>
        <ListItem>
        <ListItemAvatar>
          <Avatar>
            <SchoolIcon />
          </Avatar>
        </ListItemAvatar>
          <ListItemText primary="2018 IoT  BEACON FUN Reloaded " 
                        secondary={
                          <React.Fragment>
                            <Typography>
                              <a href="https://hanaseru.herokuapp.com/" target="_blank" rel="noopener noreferrer">web</a>
                              </Typography>
                            <Typography>
                              <a href="https://www.fun.ac.jp/wp-content/uploads/2020/03/poster14-1.pdf" target="_blank" rel="noopener noreferrer"></a>
                            </Typography>
                            <Typography>
                              <a href="https://www.fun.ac.jp/wp-content/uploads/2020/03/document14_A-1.pdf" target="_blank" rel="noopener noreferrer"></a>
                            </Typography>
                          </React.Fragment>
                        }>
            </ListItemText>
        </ListItem>
        <ListItem>
          <ListItemAvatar>
            <Avatar>
              <SettingsVoiceIcon />
            </Avatar>
          </ListItemAvatar>
          <ListItemText primary="Alexa  ()" 
                        secondary={
                                   <React.Fragment>
                                     <Typography>
                                       <a href="https://www.amazon.co.jp/Tmori-%E3%82%86%E3%81%A7%E5%8D%B5%E3%82%BF%E3%82%A4%E3%83%9E%E3%83%BC/dp/B07GZGB98L"> (Amazon)</a>
                                     </Typography>
                                   </React.Fragment>
                                  }>
          </ListItemText>
        </ListItem>
        <ListItem>
        <ListItemAvatar>
          <Avatar>
            <SettingsVoiceIcon />
          </Avatar>
        </ListItemAvatar>
        <ListItemText primary="LINE Clova  ~~" 
                      secondary={
                                   <React.Fragment>
                                     <Typography>
                                       <a href="https://www.amazon.co.jp/Tmori-%E3%82%86%E3%81%A7%E5%8D%B5%E3%82%BF%E3%82%A4%E3%83%9E%E3%83%BC/dp/B07GZGB98L"> (LINE Clova )</a>
                                     </Typography>
                                   </React.Fragment>
                                  }>
          </ListItemText>
        </ListItem>
      </List>

      </Container>

      <footer className={classes.footer}>
        <Container maxWidth="sm">
          <Typography variant="body1">Yuto Mori's portfolio.</Typography>
          <Copyright />
        </Container>
      </footer>
    </div>
  );
}